.headerText {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 9px;
    padding: 39px 20px;

}

.line,
.lineDark {
    height: 2px;
    flex: 1;
}

.line {
    background: #0f172a;
}

.lineDark {
    background: white;
}

.text,
.textDark {
    font-weight: 600;
    font-size: 2.3rem;
    -webkit-text-fill-color: transparent;
}

.text {
    -webkit-text-stroke: 1.6px #0f172a;
}

.textDark {
    -webkit-text-stroke: 1.6px white;
}

@media (min-width: 640px) {

    .text,
    .textDark {
        font-size: 4rem;
        -webkit-text-width: 2px #0f172a;
    }
}